import React, { useState } from "react";

import { CustomInput, CustomButton } from "../../components";
import { contactData } from "../../constants";

import { images } from "../../images";

const ContactUsSection = () => {
  const [formData, setFormData] = useState<{
    email: string;
    phone: string;
    fullName: string;
  }>({ email: "", phone: "", fullName: "" });

  const handleOnClick = () => {
    console.log(formData);
  };

  const onChangeField = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    setFormData((prevState) => ({ ...prevState, [name]: value }));
  };

  return (
    <div className="w-full bg-contact-us bg-cover">
      <div className="flex flex-col items-center justify-end h-full py-[24px]">
        <p className="font-ubuntu font-[700] md:text-[64px] md:leading-[74px] sm:text-[48px] sm:leading-[62px] text-[40px] leading-[50px] text-center mb-8 text-[#292929]">
          Ми зателефонуємо
          <br /> Вам!{" "}
          <img
            src={images.contactUsPhoneIcon}
            className="inline-block ml-2 align-middle md:h-[80px] md:w-[80px] sm:h-[64px] sm:w-[64px] h-[40px] w-[40px]"
            alt="contact-us-phone-icon"
          />
        </p>
        <div className="relative flex items-center justify-center md:w-[700px] md:h-[500px] mb-4">
          <div className="absolute inset-0 bg-[#474747] opacity-70 blur-sm sm:rounded-[16px]"></div>
          <div className="relative z-10 flex h-full w-[70%] flex-col sm:mt-12 md:mt-16 lg:mt-24 xl:mt-28 mt-6">
            <p className="text-white font-ubuntu font-[400] sm:text-[22px] sm:leading-[25px] text-center">
              Введіть свій номер телефону, і ми зв’яжемося з вами найближчим
              часом.
            </p>
            <div className="flex flex-col justify-center items-center my-8">
              {contactData.map((field) => (
                <CustomInput
                  key={field.name}
                  placeholder={field.placeholder}
                  value={formData ? formData[field.name] : field.placeholder}
                  name={field.name}
                  onChange={(e) => onChangeField(e)}
                />
              ))}
              <CustomButton
                onClick={handleOnClick}
                title="Зателефонуйте мені"
              />
            </div>
          </div>
        </div>
        <img
          src={images.contactUsLogoHorizontal}
          alt="contact-us-logo-horizontal"
        />
      </div>
    </div>
  );
};

export default ContactUsSection;
